  font-style: normal;
  margin-top: 0;
  background-color: #f4f7f9; }

.list {
  list-style: none;
  margin: 0;
  padding: 0; }

li {
  margin: 0;
  padding: 0; }

.title {
  font-size: 14px;
  line-height: 1.5em; }

.text {
  font-size: 14px;
  line-height: 1.5em; }

.link {
  font-size: 14px;
  text-decoration: underline dotted;
  text-decoration-color: #a5be00;
  color: #a5be00; }

@media (min-width: 768px) {
  .title {
    font-size: 1.3rem; }
  .text {
    font-size: 1.3rem; }
  .link {
    font-size: 1.3rem;
    color: #a5be00; } }

@media (min-width: 1200px) {
  .title,
  .text {
    font-size: 1.65rem; }
  .link {
    font-size: 1.65rem; } }

.page-main {
  display: block;
