  .wrapper {
    max-width: 40em; } }

.header-name {
  margin-bottom: 2em; }
  .header-name__name {
    font-size: 28px;
    margin: 0; }

@media (min-width: 768px) {
  .header-name {
    margin-bottom: 4em; }
    .header-name__name {
      font-size: 40px; } }

.caption {
  box-sizing: border-box;
  color: rgba(99, 99, 99, 0.9);
  background-color: #f4f7f9;
  border-radius: 0;
  padding-bottom: 10px; }
  .caption__text {
    box-sizing: border-box;
    margin: 0;
    margin-bottom: 25px; }
  .caption__link {
    box-sizing: border-box;
    font-style: italic;
